/* reset */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
nav ul,
nav li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  /* vertical-align: baseline; */
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
  margin: 0px;
  padding: 0px;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* start editing from here */
a {
  text-decoration: none;
}

.txt-rt {
  text-align: right;
}

/* text align right */
.txt-lt {
  text-align: left;
}

/* text align left */
.txt-center {
  text-align: center;
}

/* text align center */
.float-rt {
  float: right;
}

/* float right */
.float-lt {
  float: left;
}

/* float left */
.clear {
  clear: both;
}

/* clear float */
.pos-relative {
  position: relative;
}

/* Position Relative */
.pos-absolute {
  position: absolute;
}

/* Position Absolute */
.vertical-base {
  vertical-align: baseline;
}

/* vertical align baseline */
.vertical-top {
  vertical-align: top;
}

/* vertical align top */
nav.vertical ul li {
  display: block;
}

/* vertical menu */
nav.horizontal ul li {
  display: inline-block;
}

/* horizontal menu */
img {
  max-width: 100%;
}

/*end reset*/
/*--header--*/

/*--//header--*/

/*--main--*/

.sub-main-w3 input[type="text"],
.sub-main-w3 input[type="password"] {
  outline: none;
  font-size: 0.9em;
  padding: 1em 5em 1em 1em;
  border: none;
  margin-bottom: 1.3em;
  background: rgba(255, 255, 255, 0.85);
  color: #000;
  border-radius: 30px;
  font-weight: 400;
  letter-spacing: 1px;
}

.sub-main-w3 {
  width: 90%;
  margin: 2em auto 12em;
}

.sub-w3l h6 a:hover {
  color: #fb4226;
  transition: 1s all;
}

.register a {
  font-size: 0.9em;
  color: #fff;
  letter-spacing: 1px;
  float: left;
  display: inherit;
}

.forgotpass a {
  font-size: 0.9em;
  color: #fff;
  letter-spacing: 1px;
  float: right;
  display: inherit;
}

.sub-w3l {
  margin-top: 1em;
}

.pom-agile {
  position: relative;
}

span.icon1,
span.icon2 {
  color: #fb4226;
  font-size: 1.1em;
  position: absolute;
  right: 11%;
}

span.icon1 {
  top: 23%;
}

span.icon2 {
  top: 21%;
}

.sub-main-w3 input[type="submit"] {
  margin-top: 1rem;
  color: white;
  background: #fb4226;
  border: none;
  padding: 0.7em 0;
  outline: none;
  width: 44%;
  font-size: 1em;
  cursor: pointer;
  letter-spacing: 1px;
  font-weight: 400;
  font-family: "Open Sans", sans-serif;
  transition: 0.5s all;
  -webkit-transition: 0.5s all;
  -o-transition: 0.5s all;
  -moz-transition: 0.5s all;
  -ms-transition: 0.5s all;
  border-radius: 30px;
}

.sub-main-w3 input[type="submit"]:hover {
  background: #fff;
  color: black;
  transition: 0.5s all;
  -webkit-transition: 0.5s all;
  -o-transition: 0.5s all;
  -moz-transition: 0.5s all;
  -ms-transition: 0.5s all;
}

/*--//main--*/

/*--footer--*/
.footer p {
  font-size: 0.9em;
  color: white;
  letter-spacing: 2px;
}

.footer p a {
  color: #fff;
}

.footer p a:hover {
  color: #ea2858;
}

/*--//footer--*/

